import React, { useState } from 'react';
import './App.css';
import Search from './components/search/search.js';
import Forecast from './components/forecast/forecast.js';
import CurrentWeather from './components/current-weather/current-weather.js';
import WeatherNews from './components/WeatherNews/WeatherNews.js';
import WeatherGraph from './components/WeatherGraph/WeatherGraph.js';
import fetchAndSummarizeWeatherNews from './services/gemini-service.js';
import { REACT_APP_WEATHER_API_URL, REACT_APP_WEATHER_API_KEY } from './api.js';

function App() {
  // Existing state variables
  const [currentWeather, setCurrentWeather] = useState(null);
  const [forecast, setForecast] = useState(null);
  const [weatherNewsSummary, setWeatherNewsSummary] = useState(null);
  const [flagUrl, setFlagUrl] = useState(null);
  const [hourlyForecast, setHourlyForecast] = useState([]);

  // --- New State Variable ---
  // State to track if data is currently being fetched
  const [isLoading, setIsLoading] = useState(false);
  // Optional: State to specifically track errors
  const [error, setError] = useState(null);

  const handleOnSearchChange = (searchData) => {
    // --- Start Loading ---
    setIsLoading(true);
    setError(null); // Clear previous errors
    // Clear previous data immediately for better UX
    setCurrentWeather(null);
    setForecast(null);
    setHourlyForecast([]);
    setFlagUrl(null);
    setWeatherNewsSummary(null);

    const [lat, lon] = searchData.value.split(" ");

    const CurrentWeatherFetch = fetch(
      `${REACT_APP_WEATHER_API_URL}/weather?lat=${lat}&lon=${lon}&units=metric&appid=${REACT_APP_WEATHER_API_KEY}`
    );

    const ForecastWeatherFetch = fetch(
      `${REACT_APP_WEATHER_API_URL}/forecast?lat=${lat}&lon=${lon}&units=metric&appid=${REACT_APP_WEATHER_API_KEY}`
    );

    Promise.all([CurrentWeatherFetch, ForecastWeatherFetch])
      .then(async (response) => {
        // Check if BOTH responses are ok
        if (!response[0].ok || !response[1].ok) {
          // Try to get error details if available
          let errorMsg = "Failed to fetch weather data";
          try {
             if (!response[0].ok) errorMsg = `Weather API Error: ${response[0].status} ${await response[0].text()}`;
             else if (!response[1].ok) errorMsg = `Forecast API Error: ${response[1].status} ${await response[1].text()}`;
          } catch (_) { /* Ignore parsing errors */ }
          throw new Error(errorMsg); // Throw error to be caught by .catch
        }

        const weatherResponse = await response[0].json();
        const forecastResponse = await response[1].json();

        // Update weather and forecast state
        setCurrentWeather({ city: searchData.label, ...weatherResponse });
        setForecast({ city: searchData.label, ...forecastResponse });

        // Filter hourly forecast data for today
        const today = new Date().toISOString().split("T")[0];
        const hourlyData = forecastResponse.list.filter((entry) =>
          entry.dt_txt.startsWith(today)
        );
        setHourlyForecast(hourlyData);

        // Fetch the flag URL
        const countryCode = weatherResponse.sys?.country || "unknown";
        const flagUrl = `https://flagcdn.com/w320/${countryCode.toLowerCase()}.png`;
        setFlagUrl(flagUrl);

        // Fetch and summarize weather news using Google Gemini (can run concurrently or after)
        // Using await here makes it sequential after weather/forecast are processed
        try {
          const newsSummary = await fetchAndSummarizeWeatherNews(searchData.label);
          setWeatherNewsSummary(newsSummary); // Update news summary state
        } catch (newsError) {
          console.error("Error fetching weather news summary:", newsError.message);
          // Set specific error for news, or append to general error
          setWeatherNewsSummary("Unable to fetch weather news summary.");
          // Optionally set the main error state too: setError("Weather data loaded, but news failed.");
        }
      })
      .catch((err) => {
        console.error("Error during data fetching:", err);
        // --- Set Error State ---
        // Set a user-friendly error message
        setError(`Error: ${err.message || "Could not fetch data. Please check your connection or API keys."}`);
        // Ensure all data states are cleared (might be redundant if cleared at start, but safe)
        setCurrentWeather(null);
        setForecast(null);
        setHourlyForecast([]);
        setFlagUrl(null);
        setWeatherNewsSummary(null);
      })
      .finally(() => {
        // --- Stop Loading ---
        // This runs regardless of success or failure
        setIsLoading(false);
      });
  };

  return (
    <div className="App">
      {/* Search component always visible */}
      <Search onSearchChange={handleOnSearchChange} />

      {/* --- Loading Indicator --- */}
      {isLoading && <div className="loading-indicator">Loading weather data...</div>}

      {/* --- Error Display --- */}
      {error && <div className="error-message">{error}</div>}

      {/* --- Main Content Area (Render only when not loading and no error) --- */}
      {!isLoading && !error && (
        <> {/* Fragment to group multiple elements */}
          <div className="main-container">
            {/* Conditionally render components based on available data */}
            {currentWeather && <CurrentWeather flagUrl={flagUrl} data={currentWeather} />}
            {/* Display news summary if available (even if there was a news-specific error message set) */}
            {weatherNewsSummary && <WeatherNews summary={weatherNewsSummary} />}
            {hourlyForecast.length > 0 && <WeatherGraph data={hourlyForecast} />}
          </div>
          {/* Render forecast if available */}
          {forecast && <Forecast data={forecast} />}
        </>
      )}
    </div>
  );
}

export default App;